<template>
  <div id="app">
    <h1>Timer</h1>
    <button v-on:click="notifyMe">🔔つうちだよ！🔔</button>
    <button v-on:click="notifySprintStart">🔔Sprint開始だよ！🔔</button>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
export default {
  name: 'app',
  components: {
    HelloWorld
  },
  methods: {
    notifyMe : function(){
      if (!("Notification" in window)) {
        alert("This browser does not support desktop notification");
      }
      else if (Notification.permission === "granted") {
            var path = require('./assets/usagi.jpg')
            var notification = new Notification("Hi there!", {icon:path, silent:true});
            new Audio(require("./assets/sprint-start.mp3")).play();
      }
      else if (Notification.permission !== 'denied') {
        Notification.requestPermission(function (permission) {
          if (permission === "granted") {
            var path = require('./assets/usagi.jpg')
            var notification = new Notification("Hi there!", {icon:path});
            new Audio(require("./assets/sprint-start.mp3")).play();
          }
        });
      }
    }
  },
  notifySprintStart : function(){
    var path = require('./assets/usagi.jpg')
    var notification = new Notification("Hi there!", {icon:path, silent:true});
    new Audio(require("./assets/sprint-start.mp3")).play();
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
